
import { defineComponent } from "vue";
import store from '@/store';
import api from '@/services/api';

// eslint-disable-next-line @typescript-eslint/no-var-requires
let Swal = require('sweetalert2/src/sweetalert2.js').default;
import { AxiosResponse } from "axios";

const Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000,
});

export default defineComponent({
  name: "Server Modules",
  data() {
    return {
        state: store.state,
        loading: true,
        modules: {},
    }
  },
  created() {
      this.refresh_modules();
  },
  methods: {
    refresh_modules() {
        this.loading = true;
        this.modules = {};
        api.get_modules().then((response: AxiosResponse) => {
            console.log(response.data);
            this.modules = response.data.modules;
            this.loading = false;
        });
    },
    set_module(modul: any, activate: boolean) {
        modul.loading = true;
        api.set_module(modul.name, activate).then(async (response: AxiosResponse) => {
            modul.error = false;
            modul.loading = false;

            await store.dispatch('update_server');

            if (activate) {
                Toast.fire({
                    icon: 'success',
                    title: 'Modul activated successful'
                });
            } else {
                Toast.fire({
                    icon: 'success',
                    title: 'Modul deactivated successful'
                });
            }
            
        }).catch((error) => {
            modul.error = true;
            modul.loading = false;

            if (activate) {
                Toast.fire({
                    icon: 'error',
                    title: error.response.data.description
                });
            } else {
                Toast.fire({
                    icon: 'error',
                    title: error.response.data.description
                });
            }
        });
    }
  }
});
